import { useState } from 'react';
import couponBg from '../assets/coupon-alert.jpg';

export default function News() {
  const [newsModalShow, setNewsModalShow] = useState(false);
  const [newsHide, setNewsHide] = useState(false);
  const [copied, setCopied] = useState(false);
  const couponCode = 'vip888';

  const closeNews = () => {
    setNewsHide(true);
  };

  const closeNewsModal = () => {
    setNewsModalShow(false);
  };

  const showNewsModal = () => {
    if (newsModalShow) {
      setNewsModalShow(false);
      return;
    }
    setNewsModalShow(true);
  };

  // 
  const handleCopyCode = () => {
    navigator.clipboard
      .writeText(couponCode)
      .then(() => {
        setCopied(true);
        setTimeout(() => setCopied(false), 2000);
      })
      .catch((err) => {
        console.error('', err);
      });
  };

  return (
    <>
      <img src={couponBg} style={{ display: 'none' }} alt="preload" />
      <div className={`coupon-news ${newsHide ? 'd-none' : ''}`}>
        <div className="news">
          <div className="newsCont">
            <p>?</p>
            <button
              type="button"
              className="btn btn-sm"
              onClick={showNewsModal}
            >
              
            </button>
            <button
              className="newsCont-closeBtn btn"
              type="button"
              onClick={closeNews}
            >
              X
            </button>
          </div>
        </div>
        <div className={`newsModal ${newsModalShow ? 'show' : ''}`}>
          <div className="newsModal-cont border border-info border-5 mt-3">
            <div className="newsModal-header">
              <h5 className="newsModal-title fw-bold">
                 HanHowLife  
              </h5>
              <button
                className="newsModal-closeBtn btn"
                type="button"
                onClick={closeNewsModal}
              >
                X
              </button>
            </div>
            <div className="newsModal-body">
              <div
                className="img-couponAlert d-flex flex-column justify-content-center align-items-center text-white"
                style={{ backgroundImage: `url(${couponBg})` }}
              >
                <p className="bg-secondary bg-opacity-50 text-center fw-bold p-3 py-4 mb-3">
                   HanHowLife 
                  <br />  8 
                </p>
                <button
                  type="button"
                  className="btn btn-info py-2 px-5 text-white"
                  onClick={handleCopyCode}
                  style={{ backgroundColor: copied ? '#df5e4b' : '#d5978e' }}
                >
                  {copied ? '' : ''}
                </button>
              </div>
            </div>
          </div>
        </div>
      </div>
    </>
  );
}
