import { useEffect, useRef, useState, useMemo } from 'react';
import { Link, useOutletContext, useParams } from 'react-router-dom';
import InfoIcon from '../../assets/info-icon.png';
import SwiperPerView from '../../components/SwiperPerView';
import BgNotice from '../../assets/crystal-care.jpg';
import LoadingAnimation from '../../components/LoadingAnimation';
import { addCartItem, fetchProduct } from '../../apis';

export default function ProductDetail() {
  const { id } = useParams();
  const { getCart } = useOutletContext();
  const inputRef = useRef(null);

  const [product, setProduct] = useState(null);
  const [mainIndex, setMainIndex] = useState(0);
  const [isLoading, setIsLoading] = useState(false);
  const [quantityMessage, setQuantityMessage] = useState(false);

  //
  const allImages = useMemo(() => {
    return product ? [product.imageUrl, ...(product.imagesUrl || [])] : [];
  }, [product]);

  const addToCart = async () => {
    const cartQuantity = Number(inputRef.current.value || 1);

    setIsLoading(true);
    try {
      await addCartItem(product.id, cartQuantity);
      getCart();
    } catch (err) {
      console.error('', err);
    } finally {
      setIsLoading(false);
    }
  };

  //  ( Race Condition )
  useEffect(() => {
    let isIgnore = false;

    const getProduct = async () => {
      setProduct(null);
      setMainIndex(0);
      window.scrollTo(0, 0);

      try {
        const res = await fetchProduct(id);
        if (!isIgnore) {
          setProduct(res.data.product);
        }
      } catch (err) {
        console.error('ID', err);
      }
    };

    getProduct();
    return () => {
      isIgnore = true;
    }; // 
  }, [id]);

  if (!product)
    return (
      <div className="min-height">
        <LoadingAnimation />
      </div>
    );

  return (
    <>
      <div className="min-height" key={id}>
        {/* Product Details */}
        <div className="product container row g-0 px-md-3 mx-auto">
          <div className="product-photos col-md-6 row g-0 align-self-start p-3 p-md-0">
            <div className="photo-lg col-12">
              <span
                className="photo-img"
                style={{
                  backgroundImage: `url(${allImages[mainIndex] || ''})`,
                }}
              />
            </div>
            {allImages.slice(0, 4).map((url, index) => (
              <div
                className={`photo-sm col-3 ${
                  mainIndex === index ? 'active' : ''
                }`}
                key={index}
                onClick={() => setMainIndex(index)}
              >
                <span
                  className="photo-img"
                  style={{
                    backgroundImage: `url(${url})`,
                  }}
                ></span>
              </div>
            ))}
          </div>
          <div className="product-content col-md-6 text-center text-md-start">
            <div className="path d-block mb-4 fs-7">
              <Link to="/" className="breadcrumb-link">
                
              </Link>
              <span className="mx-2">/</span>
              <Link to="/products" className="breadcrumb-link">
                
              </Link>
              <span className="mx-2">/</span>
              <span className="breadcrumb-link active">{product.category}</span>
            </div>
            <h1>{product.title}</h1>
            <p className="product-desc">{product.content}</p>
            <p className="product-desc-secondary m-0">{product.description}</p>
            <p className="product-price mt-4">
              {product.price < product.origin_price ? (
                <>
                  <span>${product.price}</span>
                  <small className="del">$ {product.origin_price} NTD</small>
                </>
              ) : (
                <>
                  <small>$ {product.price} NTD</small>
                </>
              )}

              <small className="fs-7 ms-2 text-secondary">
                / {product.unit}
              </small>
            </p>
            <div className="product-btns col-md-9 d-md-flex">
              <input
                type="number"
                min="1"
                max="30"
                className="form-control product-quantity text-center text-md-start"
                inputMode="numeric"
                defaultValue={1}
                ref={inputRef}
                onChange={function (e) {
                  let targetNum = e.target.value;
                  if (targetNum <= 0) {
                    setQuantityMessage(true);
                  } else if (targetNum > 30) {
                    setQuantityMessage(true);
                  } else {
                    setQuantityMessage(false);
                  }
                }}
              />

              <button
                className="product-addBtn btn btn-primary col-12 col-md-7 ms-md-2 p-3 mt-3 mt-md-0"
                type="button"
                onClick={() => addToCart()}
                disabled={isLoading || quantityMessage}
                style={{
                  cursor: quantityMessage ? 'not-allowed' : 'pointer',
                  backgroundColor: quantityMessage ? '#919090ff' : '',
                }}
              >
                <span className={` ${isLoading ? 'd-none fade' : 'show'}`}>
                  
                </span>
                <div
                  className={`bounceBall ${isLoading ? 'show' : 'd-none fade'}`}
                >
                  <span></span>
                  <span></span>
                  <span></span>
                </div>
              </button>
            </div>
            {quantityMessage ? (
              <small className="fs-7 ms-2 text-danger quantity-message">
                130
              </small>
            ) : (
              ''
            )}
          </div>
        </div>
        {/* Product Description  */}
        <div className="product-info">
          <div className="container mx-auto p-3 py-md-0">
            <div className="product-info-text p-3 px-md-0 py-md-5 lh-lg">
              <h2 className="fs-5 text-primary"></h2>
              <p className="mb-4">
                HanHowLife
                
              </p>
              <ul className="list-unstyled fa-ul m-0 mt-3">
                <li className="list-item">
                  <span className="fa-li">
                    <img src={InfoIcon} className="info-icon" />
                  </span>
                  
                </li>
                <li className="list-item">
                  <span className="fa-li">
                    <img src={InfoIcon} className="info-icon" />
                  </span>
                  
                  <ul>
                    <li></li>
                    <li></li>
                    <li></li>
                  </ul>
                </li>
                <li className="list-item">
                  <span className="fa-li">
                    <img src={InfoIcon} className="info-icon" />
                  </span>
                  
                </li>
                <li className="list-item">
                  <span className="fa-li">
                    <img src={InfoIcon} className="info-icon" />
                  </span>
                  
                </li>
              </ul>
            </div>
            <div className="bg-notice">
              <img className="object-cover" src={BgNotice} alt="" />
            </div>
          </div>
        </div>
        {/* Related Products */}
        <div className="related-products container px-3 mb-5">
          <h2 className="fs-4 text-center my-5"></h2>
          <SwiperPerView spaceBetween="30" category={product.category} />
        </div>
      </div>
    </>
  );
}
